@import url(https://fonts.googleapis.com/css2?family=PT+Mono&family=Source+Sans+Pro:wght@200;400;700&family=Tillana:wght@400;600&display=swap);
.display{text-align:center;padding:10px;line-height:1em;min-height:calc(1em + 20px);background-color:#d3d3d3;border-radius:5px;box-shadow:1px 1px 3px #a9a9a9 inset,-1px -1px 3px #a9a9a9 inset}.display.disabled{background-color:#a9a9a9}
.power-switch{background:none;border:none;text-align:center;font-size:2rem}.power-switch.on{color:green}.power-switch.off{color:red}
.volume-panel{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;grid-gap:.5rem;gap:.5rem}.volume-slider{width:80%}
.radio-option{display:block;margin:.5rem}.radio-input{position:relative;vertical-align:bottom;top:-0.05rem}
.drum-pad{text-align:center;width:100%;height:100%;border-radius:5px}
#drum-machine{display:grid;grid-template-columns:repeat(3, 10vw) calc(30vw + 20px);grid-template-rows:repeat(2, -webkit-max-content) repeat(3, 10vw);grid-template-rows:repeat(2, max-content) repeat(3, 10vw);grid-template-areas:"hd hd hd hd" "dis dis dis pow" "Q W E vol" "A S D bs" "Z X C bs";grid-gap:10px;gap:10px;padding:20px;border-radius:10px;box-shadow:1px 1px 1px #a9a9a9,-1px -1px 1px #a9a9a9}#drum-machine.mobileView{margin:10px;padding:10px;grid-template-columns:repeat(3, calc( calc(100vw - 70px) / 3));grid-template-rows:repeat(4, -webkit-max-content) repeat(3, calc( calc(100vw - 70px) / 3)) -webkit-max-content;grid-template-rows:repeat(4, max-content) repeat(3, calc( calc(100vw - 70px) / 3)) max-content;grid-template-areas:"hd hd hd" "pow pow pow" "vol vol vol" "dis dis dis" "Q W E" "A S D" "Z X C" "bs bs bs"}#drum-machine>h3{grid-area:hd;text-align:center;margin-bottom:.5em}#display{grid-area:dis}#power-switch{grid-area:pow}#volume-panel{grid-area:vol}#bank-select{grid-area:bs;align-self:center;justify-self:center}#drumpad-Q{grid-area:Q}#drumpad-W{grid-area:W}#drumpad-E{grid-area:E}#drumpad-A{grid-area:A}#drumpad-S{grid-area:S}#drumpad-D{grid-area:D}#drumpad-Z{grid-area:Z}#drumpad-X{grid-area:X}#drumpad-C{grid-area:C}
footer{padding-bottom:1rem;text-align:center}
.disabled{opacity:.25}#react-app{height:100vh;width:100vw;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}main{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:"Source Sans Pro",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:"PT Mono",monospace}
